.header {
    background-color: lightsteelblue;
}

.stickyHeader {
    position: sticky;
    top     : 0;

    background-color: lightgoldenrodyellow;
}


.logo {
    height    : 70%;
    margin-top: 7px;
}

.topHeader {
    margin: auto;
    height: 100%;

    background-color: mediumaquamarine;
}

.bottomHeader {
    margin: auto;
    height: 100%;

    background-color: lime;
}

.contentWidth {
    width: 1500px;
}

@media (min-width: 1515px) {
    .contentWidth {
        width: 1500px;
    }
}